// GENERAL //
// ////////// //
.content{
  margin-top: 70px;
}
section {
  padding-left: 40px;
  padding-right: 40px;
}

@media (min-width:1200px) {
  .section-content{
    max-width: 1400px;
    margin: 0 auto;
  }
}
@media (max-width: 1199px) {
  section {
    padding-left: 20px;
    padding-right: 20px;
  }
}
body {
  font-family: "Roboto", sans-serif;
}

h1, .h1 {
  font-size: 96px;
  line-height: 1.2em;
}
h2, .h2{
  font-size: 40px;
  line-height: 1.2em;
}
h3, .h3{
  font-size: 24px;
}
h4, .h4{
  font-size: 20px;
}
h5, .h5{
  font-size: 18px;
}
.title-wrapp {
  font-weight: 700;
}
@media (max-width: 991px) {
  h2, .h2{
    font-size: 36px;
  }
  h3, .h3{
    font-size: 20px;
  }
  h4, .h4{
    font-size: 18px;
  }
  h5, .h5{
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  h2, .h2{
    font-size: 30px;
  }
  h3, .h3{
    font-size: 18px;
  }
  h4, .h4{
    font-size: 16px;
  }
}
@media (max-width: 575px) {
  h2, .h2{
    font-size: 24px;
  }
}

p {
  font-size: 16px;
}
// Spacer
.spacer-xxl {
  height: 150px;
}
.spacer-xl {
  height: 120px;
}
.spacer-lg {
  height: 100px;
}
.spacer-md {
  height: 80px;
}
.spacer-sm {
  height: 50px;
}
.spacer-xs {
  height: 30px;
}
.spacer-ss {
  height: 15px;
}
/*media*/
@media (max-width: 1399px) {
  .spacer-xxl,
  .spacer-xl {
    height: 100px;
  }
  .spacer-lg,
  .spacer-md {
    height: 50px;
  }
  .spacer-lg {
    height: 40px;
  }
  .sm-40 {
    height: 40px;
  }
}
@media (max-width: 1199px) {
  .spacer-xxl,
  .spacer-xl {
    height: 80px;
  }
}

@media (max-width: 767px) {
  .spacer-xxl,
  .spacer-xl,
  .spacer-lg,
  .spacer-md,
  .spacer-sm {
    height: 50px;
  }
  .sm-40 {
    height: 30px;
  }
}
.left-content {
  display: flex;
  .left-block {
    width: 100%;
  }
  .right-block {
    width: 50%;
  }
}
@media (max-width: 1199px) {
  .left-content {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;

    display: flex;
    .left-block {
      width: 100%;
    }
    .right-block {
      width: 100%;
    }
  }
}
.right-content {
  display: flex;
  .left-block {
    width: 50%;
  }
  .right-block {
    width: 50%;
    .right-img {
      img {
        height: 350px;
      }
    }
  }
}
@media (max-width: 1199px) {
  .right-content {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;

    display: flex;
    .left-block {
      width: 100%;
    }
    .right-block {
      width: 100%;
    }
  }
}

// content-blc-full
.content-blc-full {
  height: 600px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.content-blc-wrapp {
  width: 70%;
  margin: 0 auto;
  text-align: center;
  font-weight: 500;
  .content-blc-title{
    line-height: 1.5;
    font-weight: 600;
    margin-bottom: 50px;
  }
}
.content-blc-wrapp>*{
  margin-bottom: 30px;
}
.content-blc-wrapp>*:last-child{
  margin-bottom: 0
}
@media (max-width: 991px) {
  .content-blc-full {
    height: 450px;
  }
}
